.aside {
  grid-area: aside;
  box-shadow: 26px 9px 10px -8px rgba(0, 0, 0, 0.1);
  background: #060026;
  padding: 30px 1.5rem 1.5rem;
}
@media (max-width: 1546px) {
  .aside {
    padding: 24px 1.5rem 1.5rem;
  }
}
.logo-link {
  width: 140px;
  height: 39px;
}
.section-nav {
  padding-left: 20px;
}
.section-nav__item {
  position: relative;
  color: rgba(225, 223, 236, 0.5);
  margin-bottom: 30px;
  transition: all 0.4s ease-out;
}
.section-nav__item:hover,
.section-nav__item:focus,
.section-nav__item:active,
.section-nav__item.active {
  color: #EB00FF;
}
.section-nav__item.active::before {
  position: absolute;
  content: '';
  width: 22px;
  height: 22px;
  left: -54px;
  background: #EB00FF;
  box-shadow: #ea00ff70 1px 0px 30px 13px;
  border-bottom-right-radius: 500%;
  border-top-right-radius: 500%;
  animation: FadeIn 1s linear;
}
.section-nav__item span {
  color: rgba(225, 223, 236, 0.5);
  margin-left: 4px;
  transition: all 0.4s ease-out;
}
.section-nav__item span:hover,
.section-nav__item span:focus,
.section-nav__item span:active,
.section-nav__item.active span {
  color: #EB00FF;
}
.section-nav .icon {
  stroke: #747089;
  fill: #747089;
  transition: all 0.4s ease-out;
}
.section-nav__item:hover .icon,
.section-nav__item:focus .icon,
.section-nav__item:active .icon,
.section-nav__item.active .icon {
  stroke: #EB00FF;
}
@keyframes FadeIn {
  from {
    transform: scale(10%);
  }
  to {
    transform: scale(100%);
  }
}
.navbar-heading {
  font-size: 0.75rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  padding: 0.5rem 1rem;
  color: #8898aa;
  margin-left: 20px;
  margin-bottom: 20px;
}
.help-btn {
  margin-top: 90%;
  display: inline-block;
  width: 100%;
  padding: 16px 32px;
}
